@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap);
/* 구글 로보토폰트 가져오기 */
* {
    margin: 0;
    padding: 0;
    outline: none;
    font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

/* Auth */
.container1 {
    width: 100%;
    max-width: 320px;
    display: flex;
    flex-direction: column;
  }
.authContainer {
    display: flex;
    flex-direction: column;
    height: 100vh;
    justify-content: center;
    align-items: center;
  }
  
  .authBtns {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 320px;
  }
  
  .authBtn {
    cursor: pointer;
    border-radius: 20px;
    border: none;
    padding: 10px 0px;
    font-size: 12px;
    text-align: center;
    width: 150px;
    background: white;
    cursor: pointer;
  }
  
  /* AuthForm */
  
  .authInput {
    max-width: 320px;
    width: 100%;
    padding: 10px;
    border-radius: 30px;
    background-color: rgba(255, 255, 255, 1);
    margin-bottom: 10px;
    font-size: 12px;
    color: black;
  }
  
  .authSubmit {
    text-align: center;
    background: #04aaff;
    color: white;
    margin-top: 10;
    cursor: pointer;
  }
  
  .authError {
    color: tomato;
    text-align: center;
    font-weight: 500;
    font-size: 12px;
  }
  
  .authSwitch {
    color: #04aaff;
    cursor: pointer;
    margin-top: 10px;
    margin-bottom: 50px;
    display: block;
    font-size: 12px;
    text-decoration: underline;
  }
.about__container {
  padding: 50px;
  font-size: 25px;
}

